<template>
    <div id="ratingSelect">
        <div class="rating-type">
            <div @click="select(2)" class="block positive" :class="{'active': Stype===2}">
                {{desc.all}}
                <span class="count">{{ratings.length}}</span>
            </div>
            <span @click="select(0)" class="block positive" :class="{'active': Stype===0}">
                {{desc.positive}}
                 <span class="count">{{positives.length}}</span>
            </span>
            <span @click="select(1)" class="block negative" :class="{'active': Stype===1}">
                {{desc.negative}}
                 <span class="count">{{negatives.length}}</span>
            </span>
        </div>
        <div @click="toggleContent" class="switch" :class="{'on' : Ocontent}">
            <i class="iconfont icon-selectfill"></i>
            <span class="text">只看有内容的评价</span>
        </div>
    </div>
</template>

<script>
const POSITIVE = 0
const NEGATIVE = 1
const ALL = 2

export default {
  name: 'ratingSelect',
  props: {
    ratings: {
      type: Array,
      default () {
        return []
      }
    },
    selectType: {
      type: Number,
      default: ALL
    },
    onlyContent: {
      type: Boolean,
      default: false
    },
    desc: {
      type: Object,
      default () {
        return {
          all: '全部',
          positive: '满意',
          negative: '不满意'
        }
      }
    }
  },
  data () {
    return {
      Stype: this.selectType,
      Ocontent: this.onlyContent
    }
  },
  methods: {
    select (type) {
      this.Stype = type
      this.$emit('Stype', type)
    },
    toggleContent () {
      this.Ocontent = !this.Ocontent
      this.$emit('Ocontent', this.Ocontent)
    }
  },
  computed: {
    positives () {
      return this.ratings.filter((rating) => {
        return rating.rateType === POSITIVE
      })
    },
    negatives () {
      return this.ratings.filter((rating) => {
        return rating.rateType === NEGATIVE
      })
    }
  }
}
</script>

<style lang="stylus" scoped>
    @import "//at.alicdn.com/t/font_1227783_5x97rodrqru.css"
    @import "../../assets/stylus/mixin.styl";
    #ratingSelect
        .rating-type
            padding 18px 0
            margin 0 18px
            border-1px(rgba(7, 17, 27, 0.1))
            font-size 0
            .block
                display inline-block
                padding 8px 12px
                margin-right 8px
                border-radius 1px
                line-height 16px
                font-size 12px
                color rgb(77, 85, 93)
                &.active
                    color #fff
                .count
                    font-size 8px
                    margin-left 2px
                &.positive
                    background rgba(0, 160, 220, 0.2)
                    &.active
                        background rgb(0, 160, 220)
                &.negative
                    background rgba(77, 85, 93, 0.2)
                    &.active
                        background  rgb(77, 85, 93)

        .switch
            padding 12px 18px
            line-height 24px
            border-bottom 1px solid rgba(7, 17, 27, 0.1)
            color rgb(147, 153, 159)
            font-size 0
            &.on
                .icon-selectfill
                    color #00c850
            .icon-selectfill
                display inline-block
                vertical-align top
                margin-right 4px
                font-size 24px
            .text
                display inline-block
                vertical-align top
                font-size 12px

</style>



